import { Component, Injector, OnInit } from '@angular/core';
import { MatPaginatorIntl, PageEvent } from '@angular/material/paginator';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { BaseResourceModel } from 'app/shared/models/base-resource.model';
import { IFilter } from 'app/shared/models/interface.model';
import { BaseResourceService } from 'app/shared/services/base-resource.service';
import { RulesService } from 'app/shared/services/rules.service';
import toastr from 'toastr';

@Component({
  template: '',
})
export abstract class BaseResourceListComponent<T extends BaseResourceModel, R>
  implements OnInit
{
  resources: T[] = [];

  // MatPaginator Inputs
  length = 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 25, 100];

  // MatPaginator Output
  pageEvent: PageEvent;
  protected _fuseConfirmationService: FuseConfirmationService;
  protected _matPaginatorIntl: MatPaginatorIntl;
  protected _ruleService: RulesService;

  constructor(
    protected injecto: Injector,
    protected resourceService: BaseResourceService<T, R>
  ) {
    this._fuseConfirmationService = this.injecto.get(FuseConfirmationService);
    this._matPaginatorIntl = this.injecto.get(MatPaginatorIntl);
    this._ruleService = this.injecto.get(RulesService);
  }

  ngOnInit(): void {
    this.carregaInformacoesResource();
    this._matPaginatorIntl.itemsPerPageLabel = 'Itens por página';
  }

  setPageSizeOptions(setPageSizeOptionsInput: string) {
    if (setPageSizeOptionsInput) {
      this.pageSizeOptions = setPageSizeOptionsInput
        .split(',')
        .map((str) => +str);
    }
  }

  carregaInformacoesResource(): void {
    const filtro: IFilter = {
      filtro: '',
      pagina: 0,
      direcaoOrdenacao: 'DESC',
      tamanho: 10,
      ordenarPor: 'id',
    };
    this.resourceService.getAll(filtro).subscribe((data) => {
      this.resources = data['content'];
      this.length = data['totalElements'];
      this.pageSize = data['size'];
    });
  }

  excluir(resource: T): void {
    const dialogRef = this._fuseConfirmationService.open({
      title: `Deseja realmente excluir o item: ${resource.id}?`,
      message: '',
      icon: {
        show: true,
        name: 'heroicons_outline:exclamation',
        color: 'warn',
      },
      actions: {
        confirm: {
          show: true,
          label: 'Confirmar',
          color: 'warn',
        },
        cancel: {
          show: true,
          label: 'Cancelar',
        },
      },
      dismissible: false,
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result === 'confirmed') {
        this.resourceService.delete(resource.id).subscribe(
          (res) => {
            toastr.success(`Item excluído com sucesso!`, '', {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            });
            this.carregaInformacoesResource();
          },
          (error) => {
            toastr.error(
              'Não foi possível excluir o perfil',
              error.error.error,
              {
                timeOut: 10000,
                progressBar: true,
                closeButton: true,
              }
            );
          }
        );
      }
    });
  }

  paginacao(event: Event): void {
    this.pageSize = event['pageSize'];
    const filtro: IFilter = {
      filtro: '',
      pagina: event['pageIndex'],
      direcaoOrdenacao: 'DESC',
      tamanho: this.pageSize,
      ordenarPor: 'id',
    };

    this.resourceService.getAll(filtro).subscribe((data) => {
      this.resources = data['content'];
      this.length = data['totalElements'];
      this.pageSize = data['size'];
    });
  }

  filtrarResource(termo: string): void {
    const filtro: IFilter = {
      filtro: termo,
      pagina: 0,
      direcaoOrdenacao: 'DESC',
      tamanho: 10,
      ordenarPor: 'id',
    };
    this.resourceService.getAll(filtro).subscribe((data) => {
      this.resources = data['content'];
      this.length = data['totalElements'];
      this.pageSize = data['size'];
    });
  }

  validaPermissao(rule: string): boolean {
    return this._ruleService.temPermissao(rule);
  }
}
