import { Injectable } from '@angular/core';
import { AuthUtils } from 'app/core/auth/auth.utils';
import { ELocalStorage } from '../models/localstorage.model';

@Injectable({
  providedIn: 'root',
})
export class RulesService {
  constructor() {}

  temPermissao(rule: string): boolean {
    const token = localStorage.getItem(ELocalStorage.TOKEN);
    if (!token) {
      return false;
    }

    const { ROLES } = AuthUtils.decodeToken(token);

    if (Object(ROLES).find((permissao) => permissao == rule)) {
      return true;
    }

    return false;
  }
}
